"use client";

import { useState, useEffect } from "react";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Mail, RefreshCcw, AlertCircle, CheckCircle } from "lucide-react";

type GmailAccount = {
  id: string;
  emailAddress: string;
  name: string;
  createdAt: string;
};

export default function GmailStatus() {
  const [status, setStatus] = useState<{
    isConnected: boolean;
    count: number;
    accounts: GmailAccount[];
  } | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const checkStatus = async () => {
    try {
      setLoading(true);
      setError(null);
      console.log("Checking Gmail connection status...");

      const response = await fetch("/api/accounts/gmail/status");

      if (!response.ok) {
        throw new Error("Failed to check Gmail status");
      }

      const data = await response.json();
      setStatus(data);
      console.log("Gmail connection status:", data);
    } catch (err) {
      console.error("Error checking Gmail status:", err);
      setError(err instanceof Error ? err.message : "Unknown error occurred");
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    checkStatus();
  }, []);

  return (
    <div className="mb-4 rounded-lg border p-4">
      <div className="mb-3 flex items-center justify-between">
        <div className="flex items-center">
          <Mail className="mr-2 h-5 w-5 text-blue-500" />
          <h3 className="text-lg font-medium">Gmail Connection Status</h3>
        </div>
        <Button
          variant="outline"
          size="sm"
          onClick={checkStatus}
          disabled={loading}
        >
          <RefreshCcw
            className={`mr-1 h-4 w-4 ${loading ? "animate-spin" : ""}`}
          />
          Refresh
        </Button>
      </div>

      {error ? (
        <div className="flex items-center gap-2 text-destructive">
          <AlertCircle className="h-4 w-4" />
          <p className="text-sm">Error: {error}</p>
        </div>
      ) : loading ? (
        <p className="text-sm text-muted-foreground">
          Checking Gmail connection...
        </p>
      ) : status ? (
        <div>
          <div className="mb-2 flex items-center gap-2">
            <Badge variant={status.isConnected ? "success" : "destructive"}>
              {status.isConnected ? (
                <CheckCircle className="mr-1 h-3 w-3" />
              ) : (
                <AlertCircle className="mr-1 h-3 w-3" />
              )}
              {status.isConnected
                ? `Connected (${status.count} ${status.count === 1 ? "account" : "accounts"})`
                : "Not Connected"}
            </Badge>
          </div>

          {status.isConnected && (
            <div className="space-y-2">
              {status.accounts.map((account) => (
                <div
                  key={account.id}
                  className="rounded-md bg-muted p-2 text-sm"
                >
                  <div className="font-medium">{account.name}</div>
                  <div className="text-xs text-muted-foreground">
                    {account.emailAddress}
                  </div>
                  <div className="mt-1 text-xs text-muted-foreground">
                    Connected:{" "}
                    {account.createdAt
                      ? new Date(account.createdAt).toLocaleString()
                      : "Recently"}
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      ) : null}
    </div>
  );
}
